                    <div class="invoice__invoice-detail mt-6">
                        <h6>{{ $t('batch.batch') }}</h6>
                        <p>{{ ordersReport.batch.id }}/{{ ordersReport.batch.year }}</p>

                        <h6 class="mt-4">{{ $t('batch.dates') }}</h6>
                        <p>{{ ordersReport.batch.dateStart | date(true) }} at√© {{ ordersReport.batch.dateEnd | date(true) }}</p>
                    </div>
                </div>
            </div>

            <!-- INVOICE CONTENT -->
            <div class="p-base">
                <!-- INVOICE TASKS TABLE -->
                <vs-table hoverFlat :data="ordersReport.books">
                    <!-- HEADER -->
                    <template slot="thead">
                        <vs-th class="pointer-events-none">{{ $t('book.book') }}</vs-th>
                        <vs-th class="pointer-events-none">{{ $t('book.isbn') }}</vs-th>
