                    </template>

                    <!-- DATA -->
                    <template slot-scope="{data}">
                        <vs-tr v-for="(tr, index) in data" :key="index">
                            <vs-td :data="data[index].title">{{ data[index].title }}</vs-td>
                            <vs-td :data="data[index].isbn">{{ data[index].isbn }}</vs-td>
                            <vs-td :data="data[index].totalOrdered">{{ data[index].totalOrdered }}</vs-td>
                        </vs-tr>
                    </template>
                </vs-table>

                <!-- INVOICE SUMMARY TABLE -->
                <!-- <vs-table hoverFlat class="w-1/2 ml-auto mt-4" :data="invoiceData">
                    <vs-tr>
