        </vx-card>
    </div>
</template>

<script>

import moduleBatchManagement from '@/store/batch-management/moduleBatchManagement.js'

export default{
    data() {
        return {
            ordersReport: {
                books: [],
                batch: {},
            },
        }
    },
    computed: {

    },
    methods: {
      printInvoice() {
        window.print()
      }
    },
    components: {

    },
    mounted() {
      this.$emit("setAppClasses", "invoice-page")
    },
    created() {
      if(!moduleBatchManagement.isRegistered) {
        this.$store.registerModule('batchManagement', moduleBatchManagement)
        moduleBatchManagement.isRegistered = true
      }

      const batchId = this.$route.params.batchId

      this.$store.dispatch("batchManagement/fetchOrdersReport", batchId)
        .then(res => {
          this.ordersReport.books = res.data.data.map(data => {
            return data.attributes
          })
        })
        .catch(err => {
          console.log('error')
          console.log(err)
          if(err.response.status === 404) {
            this.batch_not_found = true
            return
          }
        })

      this.$store.dispatch("batchManagement/fetchBatch", batchId)
        .then(res => { this.ordersReport.batch = res.data.attributes })
        .catch(err => {
          if(err.response.status === 404) {
            this.batch_not_found = true
            return
          }
          console.error(err) })
    }
}
</script>

